.container-sc{
  background-color: white;
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-rows: 1fr 8fr 1fr;
  gap: 5px;

}

.header-sc {
  background-color: #fca311;
  grid-row: 1;
  grid-column: 1 /-1 ;

}


.header-sc h1 {
  color: #fff;
  font-weight: 900;
  font-family: "Montserrat", sans-serif;
  margin: 20px;
}


.content-sc{
  grid-row:2 ;
  grid-column: 1;
}

.footer{
  background-color: #14213d;
  grid-row: 3;
  grid-column: 1 / -1;
}


 .card-income {
  border-radius: 4px;
  position: relative;
  padding: 25px 15px;
  background-color: white;
  margin: 40px 5px;
  height: 400px;
  width: auto;
  box-shadow: 0 2px 5px #ccc;
